#header {
  height: 65px;
  background-color: rgb(53, 83, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  box-shadow: 0px 0px 16px 5px black;
  z-index: 1;
}

.hamburger {
  position: absolute;
  right: 15px;
}

.sideNav {
  height: 100%;
  width: 0;
  position: fixed;
  top: 0;
  right: 0;
  background-color: rgb(155, 155, 155);
  overflow-x: hidden;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  padding-top: 60px;
  display: flex;
  flex-direction: column;
  text-align: left;
  box-sizing: border-box;
}

.hamContainer {
  display: inline-block;
  cursor: pointer;
  font-size: 20px;
  position: absolute;
  right: 15px;
}

.bar1, .bar2, .bar3 {
  width: 35px;
  height: 5px;
  background-color: black;
  margin: 6px 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {opacity: 0;}

.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px);
}
#footer {
  height: 45px;
  background-color: rgb(155, 155, 155);
  width: 100%;
  margin-top: 40px;
}

#footer p {
  text-align: center;
  position: relative;
  top: 15px;
}
